@charset "UTF-8";

@import "var";
@import "ele";

html,body{
	font-family: "\5FAE\8F6F\96C5\9ED1", Arial , serif;
	height: 100%;
}

body{
	background: url($image-big-bg) #37c67e top no-repeat;
	background-size: 100% auto;
}

.wrapper{
	position: relative;
	margin: 0 auto;
	max-width: 640px;
	height: 100%;
	overflow: hidden;
}

.hide{
	display: none !important;
}

.tips{
	@include posa(null, null, rem(20), 0);
	width: 100%;
	text-align: center;
	font-size: rem(24);
	line-height: 1.3;
	color: #097a41;
}

.page{
	@include posa(0, 0, 0, 0);
	overflow: hidden;
	z-index: 1;
	&.animateIn{
		z-index: 3;
	}
}

.page-1{
	overflow-y: auto;
	-webkit-overflow-scrolling : touch;
	.head{
		margin-top: rem(16);
		position: relative;
		height: rem(280);
		//抢红包大作战下的云
		.back-cloud{
			@extend %eles-1;
			position: absolute;
			width: 100%;
			height: rem(280);
			background-position: 0 rem(- $eles-1-3 ) ;
		}
		//抢红包大作战标题
		.big-title{
			@extend %eles-1;
			position: absolute;
			width: 100%;
			height: rem(280);
			background-position: 0 0;
		}
	}
	.boundbag{
		@extend %eles-1;
		@include posa(rem(300), null, null, 0);
		width: 100%;
		height: rem(720);
		background-position: 0 rem(- $eles-1-2 );
		.say{
			@extend %eles-2;
			@include posa(rem(372), rem(20), null, null);
			width: rem(152);
			height: rem(152);
			background-position: rem(-(640 - 152)) rem(- $eles-2-4);
		}
		.btnbox{
			@include posa(null, null, rem(88), rem(180));
			.btn{
				background-position: 0 rem(- $eles-2-2 );
				width: rem(282);
				height: rem(108);
			}
		}
	}

	%gameEnd{
		margin-top: rem(-260);
		.title{
			@extend %eles-2;
			@include posa(rem(-32), null, null, 50%);
			margin-left: rem(-420 / 2);
			width: rem(420);
			height: rem(128);
		}
		.text{
			margin-top: rem(40);
			text-align: center;	
			font-size: rem(24);
			color: #fff;
		}
		.star{
			@extend %eles-2;
			position: absolute;
		}
		.star-1{
			top: rem(88);
			left: rem(-30);
			width: rem(100);
			height: rem(100);
			background-position: rem(-540) rem(- $eles-2-6);
		}
		.star-2{
			top: rem(230);
			right: rem(-20);
			width: rem(90);
			height: rem(108);
			background-position: rem(-(640 - 90)) rem(- $eles-2-8);
		}
		.star-3{
			top: rem(300);
			left: rem(-30);
			width: rem(88);
			height: rem(100);
			background-position: rem(-(640 - 88)) rem(- $eles-2-9);
		}
		.person{
			@extend %eles-2;
			margin: rem(68) auto 0 auto;
			background-position: 0 rem(- $eles-2-14);
			width: rem(356);
			height: rem(290);
		}
		.btnbox{
			@include posa(null, null, rem(30), 50%);
			margin: rem(100) 0 0 rem(-420 / 2);
			.btn{
				width: rem(420);
				height: rem(108);
			}
		}
	}

	.gameEnd-1{
		@extend %gameEnd;
		.title{
			background-position: 0 rem(- $eles-2-10);
		}
		.btnbox{
			.btn{
				background-position: rem(0) rem(- $eles-2-12);
			}
		}
	}

	.gameEnd-2{
		@extend %gameEnd;
		.title{
			background-position: 0 rem(- $eles-2-11);
		}
		.btnbox{
			.btn{
				background-position: rem(0) rem(- $eles-2-13);
			}
		}
	}

	.tips{
		position: relative;
		margin-top: rem(720);
		padding: rem(40) 0;
	}
}

.page-2{
	.title{
		@extend %eles-2;
		@include posa(rem(-44), null, null, 50%);
		background-position: 0 rem(- $eles-2-4);
		margin-left: rem(-(380 / 2));
		width: rem(380);
		height: rem(110);
	}
	.level{
		position: relative;
		display: inline-block;
		.border{
			@include radius(rem(20));
			@include shadow(inset 0 0 0 rem(6) #ffe25c);
			position: relative;
			width: rem(230);
			height: rem(210);
			border: rem(4) solid #81350f;
			background-color: #ff5858;
		}
		p{
			@extend %eles-2;
			@include posa(rem(16), 0, 0, rem(14));
			width: rem(60);
			height: rem(160);
		}
		.animate{
			@include posa(rem(-60), rem(-36), null, null);
			@include scale(0.75);
		}
		//选中Pk
		&.active{
			.border{
				&:before{
					@include posa(rem(4), rem(4), rem(4), rem(4));
					content: '';
					background: rgba(107, 23, 23, 0.5);
					z-index: 2;
					border-radius: rem(20);
					animation-duration: 1s;
  					animation-fill-mode: both;
  					animation-name: fadeIn;
				}
				&:after{
					@extend %eles-2;
					@include posa(50%, 0, 0, 50%);
					background-position: rem(-280) rem(- $eles-2-1 );
					content: '';
					margin: rem(-124 / 2) 0 0 rem(-194 / 2);
					width: rem(194);
					height: rem(124);
					z-index: 4;
					animation-duration: 1s;
  					animation-fill-mode: both;
  					animation-name: tada;
				}
			}
		}
		//coupon price
		&:after{
			@extend %eles-2;
			@include posa(null, null, rem(-15), 50%);
			transform: translate(-50%, 0);
			content: '';
			z-index: 6;
		}
		
	}
	.level-baby{
		margin-right: rem(12);
		p{
			background-position: 0 rem(- $eles-2-1 );
		}
		&:after{
			width: rem(98);
			height: rem(48);
			background-position: rem(-500) rem(- $eles-2-1 );
		}
	}
	.level-mom{
		margin-left: rem(12);
		p{
			background-position: rem(-72) rem(- $eles-2-1 );
		}
		.animate{
			@include posa(rem(-60), rem(-24), null, null);
		}
		&:after{
			width: rem(98);
			height: rem(48);
			background-position: rem(-500) rem(- $eles-2-1 - 53);
		}
	}
	.level-hulk{
		margin-top: rem(22);
		.border{
			width: rem(270);
		}
		p{
			background-position: rem(-144) rem(- $eles-2-1 );
		}
		.animate{
			@include posa(rem(-60), rem(-12), null, null);
		}
		&:after{
			width: rem(112);
			height: rem(48);
			background-position: rem(-500) rem(- $eles-2-1 - 53 - 53);
		}
	}
	.btnbox{
		@include posa(null, null, rem(28), 50%);
		margin-left: rem(-420 / 2);
		.btn{
			background-position: 0 rem(- $eles-2-3 );
			width: rem(420);
			height: rem(108);
		}
	}
}


.page-3{
	.redbag{
		padding-top: 0;
		&:before, &:after{
			@extend %eles-2;
			@include posa(null, null, rem(140), null);
			content: '';
			width: rem(120);
			height: rem(40);
		}
		&:before{
			left: rem(30);
			background-position: 0 rem(- $eles-2-7);
		}
		&:after{
			right: rem(30);
			background-position: rem(-152) rem(- $eles-2-7);
		}
	}
	.title{
		@extend %eles-2;
		@include posa(rem(-44), null, null, 50%);
		background-position: 0 rem(- $eles-2-5);
		margin-left: rem(-(300 / 2));
		width: rem(300);
		height: rem(80);
	}
	.pp{
		position: relative;
		margin-top: rem(80);
		height: rem(240);
	}
	.animate{
		position: absolute;
	}
	.animate-self{
		left: rem(40);
		top: 0;
		z-index: 5;
	}
	.animate-opponent{
		left: rem(300);
		top: rem(-46);
	}
	.countdown{
		margin-top: rem(-20);
		width: 100%;
		height: rem(44);
		line-height: rem(44);
		text-align: center;
		font-size: rem(44);
		color: #ffe25c;
		font-weight: bold;
		-webkit-text-stroke: rem(3) #80350f;
		&:after{
			@extend %eles-2;
			background-position: rem(-600) rem(- $eles-2-5);
			content: '';
			display: inline-block;
			vertical-align: rem(-2);
			width: rem(40);
			height: rem(40);
		}
		&.gameover{
			-webkit-text-stroke: 1px #80350f;
			font-size: rem(28);
			&:after{
				display: none;
			}
		}
	}
	.progress{
		margin-top: rem(12);
	}
	.text{
		@extend %eles-2;
		margin: rem(20) 0 0 rem(90);
		background-position: 0 rem(- $eles-2-6);
		height: rem(32);
	}
	.btnbox{
		@include posa(null, null, rem(28), 50%);
		margin-left: rem(-200 / 2);
		.btn{
			border-radius: 100%;
			background-position: rem(-440) rem(- $eles-2-2);
			width: rem(200);
			height: rem(200);
			&.disabled{
				@include shadow((0px 3px 0px #999, 0px 3px 6px rgba(0,0,0,.9)));
				background-color: #ccc;
			}
		}
	}
}

.page-4{
	.result-success{
		@include posa(50%, null, null, null);
		@include bgSize($images-result-success, 100% auto, no-repeat);
		margin-top: rem(-500);
		width: 100%;
		height: rem(580);
		z-index: 2;
		.num{
			@include posa(null, rem(378), rem(84), null);
			@include bgSize($images-result-success, rem(640) auto, no-repeat);
			width: rem(80);
			height: rem(88);
		}
		.num-1{
			background-position: rem(-4) rem(-580);
		}
		.num-3{
			background-position: rem(-108) rem(-580);
		}
		.num-5{
			background-position: rem(-108 - 104) rem(-580);
		}
		.num-8{
			background-position: rem(-108 - 104 - 120) rem(-580);
		}
		.num-10{
			@include posa(null, rem(378), rem(80), null);
			background-position: rem(-108 - 104 - 120 - 128) rem(-580);
		}
	}
	.btnbox{
		@include posa(50%, null, null, 50%);
		margin: rem(100) 0 0 rem(-420 / 2);
		.btn{
			margin-bottom: rem(30);
			width: rem(420);
			height: rem(108);
		}
	}
}

.page-5{
	.result-fail{
		@include posa(50%, null, null, null);
		@include bgSize($images-result-fail, 100% auto, no-repeat);
		margin-top: rem(-500);
		width: 100%;
		height: rem(580);
		z-index: 2;
	}
	.btnbox{
		@include posa(50%, null, null, 50%);
		margin: rem(100) 0 0 rem(-420 / 2);
		.btn{
			margin-bottom: rem(30);
			width: rem(420);
			height: rem(108);
		}
	}
}

#secondsdown{
	visibility: hidden;
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,0.5);
	z-index: 10;
	span {
		position: absolute;
		width: rem(400);
		height: rem(400);
		line-height: rem(400);
		left: 50%;
		top: 50%;
		margin: rem(-400 / 2) 0 0 rem(-400 / 2);
		color: #ffe25c;
		font-size: rem(160);
		text-align: center;
		font-weight: bold;
		opacity: 1;
		transition: opacity 0.4s ease;
		-webkit-perspective: 1000;
		-webkit-backface-visibility: hidden;
		-webkit-text-stroke: rem(4) #80350f;
		&.puffer{
			opacity: 0;
			transform: scale(1.6, 1.6);
			transition: all 0.4s ease-out;
		}
	}
	&.show{
		visibility: visible;
	}
}



/*!
 * Progress
 */
.progress {
	$bar-width  : rem(400);
	$bar-height : rem(40);

	position: relative;
	display: inline-block;
	padding: 1px;
	z-index: 5;
	background:#7f4922;
	width: $bar-width;
	box-shadow: 0 0 0 rem(3) #80350f, inset 0 0 0 1px #9d9d9d;
	border-radius: 10rem;
	border: rem(3) solid #ffebe1;
	
	overflow: hidden;
	%progress-bar-text{
		width: $bar-width;
		height: $bar-height;
		line-height: $bar-height;
	}
	.progress-text{
		@extend %progress-bar-text;
		@include posa(0, null, null, 0);
		display: none;
		text-align: center;
		&:first-child{
			z-index: 5;
			color: white;
		}
		&:last-child{
			z-index: 10;
			color: black;
		}
	}
	.progress-bar {
		$bg-line: rgba(245, 61, 61, 0.08);
		@extend %progress-bar-text;
		position: relative;
		z-index: 10;
		width: 0;
		overflow: hidden;
		transition: width .2s linear;
		animation: candystripe 1s linear infinite;
		border-radius: 10rem;
		box-shadow: 0 0 6px 2px rgba(255, 255, 255, 0.3) inset;
		background-color: #fef43d;
		background-image: linear-gradient(70deg,$bg-line 40%,rgba(255,255,255,0) 40%,rgba(255,255,255,0) 75%,$bg-line 75%);
		background-repeat: repeat-x;
		background-size: $bar-height $bar-height;
	}
	@at-root{
		//进度条效果
		@keyframes candystripe {
			to {background-position: -$bar-height 0;}
		}
	}
}




@import "animate";