.spinner{animation:rotator 1.4s linear infinite}
@keyframes rotator{0%{transform:rotate(0)}
100%{transform:rotate(270deg)}
}
.path{stroke-dasharray:187;stroke-dashoffset:0;transform-origin:center;animation:dash 1.4s ease-in-out infinite,colors 5.6s ease-in-out infinite}
@keyframes colors{0%{stroke:#fff}
25%{stroke:#fff}
50%{stroke:#fff}
75%{stroke:#fff}
100%{stroke:#fff}
}
@keyframes dash{0%{stroke-dashoffset:187}
50%{stroke-dashoffset:46.75;transform:rotate(135deg)}
100%{stroke-dashoffset:187;transform:rotate(450deg)}
}
.animated{animation-duration:1s;animation-fill-mode:both}
.animated.infinite{animation-iteration-count:infinite}
.animated.hinge{animation-duration:2s}
.animated.bounceIn,.animated.bounceOut,.animated.flipOutX,.animated.flipOutY{animation-duration:.75s}
@keyframes tada{from{transform:scale3d(1,1,1)}
10%,20%{transform:scale3d(.9,.9,.9) rotate3d(0,0,1,-3deg)}
30%,50%,70%,90%{transform:scale3d(1.1,1.1,1.1) rotate3d(0,0,1,3deg)}
40%,60%,80%{transform:scale3d(1.1,1.1,1.1) rotate3d(0,0,1,-3deg)}
to{transform:scale3d(1,1,1)}
}
.tada{animation-name:tada}
@keyframes bounceInRight{60%,75%,90%,from,to{animation-timing-function:cubic-bezier(.215,.61,.355,1)}
from{opacity:0;transform:translate3d(3000px,0,0)}
60%{opacity:1;transform:translate3d(-25px,0,0)}
75%{transform:translate3d(10px,0,0)}
90%{transform:translate3d(-5px,0,0)}
to{transform:none}
}
.bounceInRight{animation-name:bounceInRight}
@keyframes bounceOutLeft{20%{opacity:1;transform:translate3d(20px,0,0)}
to{opacity:0;transform:translate3d(-2000px,0,0)}
}
.bounceOutLeft{animation-name:bounceOutLeft}
@keyframes bounceInDown{60%,75%,90%,from,to{animation-timing-function:cubic-bezier(.215,.61,.355,1)}
0%{opacity:0;transform:translate3d(0,-3000px,0)}
60%{opacity:1;transform:translate3d(0,25px,0)}
75%{transform:translate3d(0,-10px,0)}
90%{transform:translate3d(0,5px,0)}
to{transform:none}
}
.bounceInDown{animation-name:bounceInDown}
@keyframes bounceOutUp{20%{transform:translate3d(0,-10px,0)}
40%,45%{opacity:1;transform:translate3d(0,20px,0)}
to{opacity:0;transform:translate3d(0,-2000px,0)}
}
.bounceOutUp{animation-name:bounceOutUp}
@keyframes fadeIn{from{opacity:0}
to{opacity:1}
}
.fadeIn{animation-name:fadeIn}
@keyframes fadeOut{from{opacity:1}
to{opacity:0}
}
.fadeOut{animation-name:fadeOut}
@keyframes zoomIn{from{opacity:0;transform:scale3d(.3,.3,.3)}
50%{opacity:1}
}
.zoomIn{animation-name:zoomIn}
@keyframes zoomOut{from{opacity:1}
50%{opacity:0;transform:scale3d(.3,.3,.3)}
to{opacity:0}
}
.zoomOut{animation-name:zoomOut}
@keyframes flipInX{from{transform:perspective(400px) rotate3d(1,0,0,90deg);animation-timing-function:ease-in;opacity:0}
40%{transform:perspective(400px) rotate3d(1,0,0,-20deg);animation-timing-function:ease-in}
60%{transform:perspective(400px) rotate3d(1,0,0,10deg);opacity:1}
80%{transform:perspective(400px) rotate3d(1,0,0,-5deg)}
to{transform:perspective(400px)}
}
.flipInX{backface-visibility:visible!important;animation-name:flipInX}
@keyframes flipOutX{from{transform:perspective(400px)}
30%{transform:perspective(400px) rotate3d(1,0,0,-20deg);opacity:1}
to{transform:perspective(400px) rotate3d(1,0,0,90deg);opacity:0}
}
.flipOutX{animation-name:flipOutX;backface-visibility:visible!important}