//--------------------------
// Image Elements
//--------------------------

%eles-1{
	background: url($image-eles-1) no-repeat;
	background-size: rem(640) auto;
}

%eles-2{
	background: url($image-eles-2) no-repeat;
	background-size: rem(640) auto;
}

/**
 * 按钮公共样式
 */
.btn{
	@extend %eles-2;
	display: block;
	padding: 0;
	border: none;
	box-shadow: inset 0 -0.25rem rem(10) #f0a427, inset 0 0 20px #fffb4c;
	border-radius: rem(80);
	background-color: $btn-color;
	-webkit-tap-highlight-color:transparent;
	&.active{
		position: relative;
		top: 3px;
		box-shadow: inset 5px 0.2rem rem(20) rgba(240,164,39,0.5), 0 1px 3px #f0a427;
		background-color: #fecb3d;
	}
}

/**
 * 图片公共元素
 */
.cloud{
	@include posa(rem(40), null, null, 0);
	@extend %eles-1;
	width: 100%;
	height: rem(180);
	background-position: 0 rem(-1340);
	z-index: 1;
}

.redbag{
	@include posa(50%, null, null, 50%);
	@include radius(rem(20));
	margin: rem(-(700 / 2)) 0 0 rem(-(520 / 2));
	padding-top: rem(84);
	width: rem(520);
	height: rem(700);
	background-color: #f53d3d;
	text-align: center;
	font-size: 0;
	z-index: 2;
}




.btn-again{
	background-position: rem(0) rem(- $eles-2-8);
}
.btn-share{
	background-position: rem(0) rem(- $eles-2-9);
}


