//--------------------------
// 共用功能引用 & 变量定义
//--------------------------

//引入功能
@import "base/reset";
@import "base/mixin";
@import "base/css3";
@import "base/media";


/**
 * 声明变量
 */
$btn-color: #ffea47;



/**
 * Sprite height
 */

// %eles-1
$eles-1-1: 0;
$eles-1-2: 300;
$eles-1-3: $eles-1-2 + 740;
$eles-1-4: $eles-1-3 + 300;

// %eles-2
$eles-2-1: 0;					//对手 萌娃
$eles-2-2: $eles-2-1 + 190;		//马上开抢
$eles-2-3: $eles-2-2 + 108;		//确定，本宝宝...
$eles-2-4: $eles-2-3 + 108;		//选择对手难度
$eles-2-5: $eles-2-4 + 164;		//抢枪抢
$eles-2-6: $eles-2-5 + 80;		//15秒内蓄力条满
$eles-2-7: $eles-2-6 + 40;		//快点按 不要停
$eles-2-8: $eles-2-7 + 60;		//再玩一次
$eles-2-9: $eles-2-8 + 120;		//分享给好友
$eles-2-10: $eles-2-9 + 120;	//呜呜呜 宝宝的次数用光了
$eles-2-11: $eles-2-10 + 140;	//呜呜呜 今天的次数用完了
$eles-2-12: $eles-2-11 + 140;	//快来和宝宝一起抢
$eles-2-13: $eles-2-12 + 120;	//宝宝明天再来
$eles-2-14: $eles-2-13 + 120;	//人 跪


/**
 * Image URL
 */

//大背景 640x1136
$image-big-bg: '../images/ele_big_bg.jpg';

//animate 1
$image-animate: '../images/ele_animate.png';

//animate 2
$image-animate-self: '../images/ele_animate_self.png';

//sprite 1
$image-eles-1: '../images/eles_1.png';

//sprite 2
$image-eles-2: '../images/eles_2.png';

//成功页
$images-result-success: '../images/result_success.png';

//失败页
$images-result-fail: '../images/result_fail.png';


/**
 * absolute
 * @param  {[type]} $top:null    [top]
 * @param  {[type]} $right:null  [right]
 * @param  {[type]} $bottom:null [bottom]
 * @param  {[type]} $left:null   [left]
 */
@mixin posa($top:null, $right:null, $bottom:null, $left:null){
	position: absolute;
	@if $top {
		top: $top;
	}
	@if $right {
		right: $right;
	}
	@if $bottom {
		bottom: $bottom;
	}
	@if $left {
		left: $left;
	}
}