/**
 * 人物动画
 */
.animate{
	@include bgSize($image-animate, rem(1260) auto, no-repeat);
	display: block;
	width: rem(210);
	height: rem(280);
}
.animate-baby{
	animation: ani_baby 2s steps(5) infinite;
	background-position: 0 0;
}
.animate-mom{
	animation: ani_mom 2s steps(5) infinite;
	background-position: 0 rem(-280);
}
.animate-hulk{
	animation: ani_hulk 2s steps(5) infinite;
	background-position: 0 rem(-560);
}
.animate-self{
	display: block;
	width: rem(350);
	height: rem(230);
	animation: ani_self 2s steps(5) infinite;
	background: url($image-animate-self) no-repeat 0 0;
	background-size: rem(2100) auto;
}
@keyframes ani_baby{
	50% {
		background-position: rem(-1050) 0;
	}
	100%{
		background-position: rem(-1050) 0;
	}
}
@keyframes ani_mom{
	50% {
		background-position: rem(-1050) rem(-280);
	}
	100%{
		background-position: rem(-1050) rem(-280);
	}
}
@keyframes ani_hulk{
	50% {
		background-position: rem(-1050) rem(-560);
	}
	100%{
		background-position: rem(-1050) rem(-560);
	}
}
@keyframes ani_self{
	50% {
		background-position: rem(-1750) 0;
	}
	100%{
		background-position: rem(-1750) 0;
	}
}


//loading
#loader {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: rgba(0,0,0,.5);
	display: flex;
	align-items: center;
	justify-content: center;
	&.animateOut{
		animation-name: fadeOut;
	}
	&.animateIn{
		animation-name: fadeIn;
	}
}

// Here is where the magic happens
$offset: 187;
$duration: 1.4s;

.spinner {
	animation: rotator $duration linear infinite;
}

@keyframes rotator {
	0% { transform: rotate(0deg); }
	100% { transform: rotate(270deg); }
}

.path {
	stroke-dasharray: $offset;
	stroke-dashoffset: 0;
	transform-origin: center;
	animation:
    	dash $duration ease-in-out infinite, 
    	colors ($duration*4) ease-in-out infinite;
}

@keyframes colors {
	0% { stroke: #fff; }
	25% { stroke: #fff; }
	50% { stroke: #fff; }
	75% { stroke: #fff; }
  100% { stroke: #fff; }
}

@keyframes dash {
 0% { stroke-dashoffset: $offset; }
 50% {
   stroke-dashoffset: $offset/4;
   transform:rotate(135deg);
 }
 100% {
   stroke-dashoffset: $offset;
   transform:rotate(450deg);
 }
}

